<template>
  <NavBar
    :favourites="favourites"
    :showFilter="false"
    :backHome="false"
    :title="`Recipe: ${recipe.title}`"
  />
  <article class="large-screen-wrapper">
    <p v-if="loading">loading...</p>
    <div class="mlg:grid mlg:grid-cols-3 mlg:gap-4 mlg:items-center xl:pt-10">
      <div class="relative mlg:col-span-2 w-full">
        <img class="w-full" :src="recipe.image" />
        <button
          v-on:click.prevent="favouriteCheck"
          class="absolute top-0 right-0 w-8 h-8 p-1 mr-2 mt-2 rounded-full bg-white bg-opacity-90"
          :class="favourited ? 'text-green-500' : 'text-gray-100'"
          :aria-labelledby="`favourite-button-label-${recipe.id}`"
        >
          <svg
            class="w-full h-full"
            fill="currentColor"
            stroke="#48BB78"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="1.5"
              d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
            ></path>
          </svg>
          <span :id="`favourite-button-label-${recipe.id}`" hidden
            >Add recipe to your favourites list</span
          >
        </button>
      </div>
      <RecipeInfoBox
        class="mlg:inline-block hidden mlg:mr-4 border-green-500 border-2 rounded"
        :recipeId="recipe.id"
        :recipeTime="recipe.readyInMinutes"
        :author="recipe.creditsText"
        :ingredients="ingredients"
        searched="false"
      />
    </div>
    <div class="">
      <div class="ml-2 mr-2 md:ml-4 md:mr-4 md:inline-block">
        <h1 class="inline-block mt-4 mlg:mt-12">{{ recipe.title }}</h1>
        <RecipeInfoBox
          class="inline-block mlg:hidden md:float-right w-full md:w-64 mt-2 md:mt-12 md:ml-2 sm:pl-4 sm:pr-4 border-green-500 border-2 rounded"
          :recipeId="recipe.id"
          :recipeTime="recipe.readyInMinutes"
          :author="recipe.creditsText"
          :ingredients="ingredients"
          searched="false"
        />
        <span v-if="recipe.vegan" class="text-green-700 pl-1">&#x24E5;</span>
        <p class="pt-2">
          They're a little contentious, but it's common for online recipes to be
          prefaced with a fairly long essay before you get to the recipe. (I'm
          50/50 on this: sometimes I'm impatient, but sometimes I learn
          something new!) The API I'm using here doesn't reliably provide this.
          So, because I'm making this for an exercise, rather than as a live
          product, I thought I'd just include some filler text here, to simulate
          the opening essay for layout purposes.
        </p>
        <p class="pt-2">
          So whilst I'm pulling the ingredient and timing information from the
          API, the information provided here is just illustrative. If you'd like
          to actually see the recipe for the current dish, you can check it out
          on the original website:
          <a
            class="border-b border-gray-900 border-dotted"
            target="_blank"
            rel="noopener"
            :href="recipe.sourceUrl"
            >{{ recipe.sourceName }}</a
          >.
        </p>
      </div>
      <div
        class="md:grid md:grid-cols-2 md:gap-4 ml-2 mr-2 md:ml-4 md:mr-4"
      >
        <div class="mt-8 p-2 border-2 border-green-500 border-dotted">
          <table class="w-full">
            <thead>
              <tr>
                <td><h3>Ingredient</h3></td>
                <td class="text-right"><h3>Quantity</h3></td>
              </tr>
            </thead>
            <tr
              v-for="ingredient of recipe.extendedIngredients"
              :key="recipe.id + '-' + ingredient.name"
              class="border-b border-gray-200"
            >
              <td>{{ ingredient.name }}</td>
              <td class="text-right">
                {{ ingredient.measures.us.amount }}
                {{ ingredient.measures.us.unitShort }}
              </td>
            </tr>
          </table>
        </div>
        <div class="mt-2 md:mt-8 p-2 border-2 border-green-500 border-dotted">
          <h3>Instructions</h3>
          <ol class="stepList pt-2">
            <li>Read the real instructions</li>
            <li>Get ingredients</li>
            <li>Do something with them</li>
            <li>Enjoy!</li>
          </ol>
        </div>
      </div>
    </div>
  </article>
  <FooterComponent />
</template>

<script>
import RecipeInfoBox from "../components/RecipeInfoBox.vue";
import NavBar from "../components/NavBar.vue";
import FooterComponent from "../components/FooterComponent.vue";

export default {
  props: {
    favourites: Array
  },
  components: {
    RecipeInfoBox,
    FooterComponent,
    NavBar
  },
  data: () => ({
    loading: true,
    recipe: {}
  }),
  computed: {
    ingredients() {
      if (this.recipe.extendedIngredients !== undefined) {
        return this.recipe.extendedIngredients.map(
          ingredient => ingredient.name
        );
      }
      return [];
    },
    favourited() {
      return this.favourites.includes(parseInt(this.$route.params.id));
    }
  },
  async created() {
    const endpoint = "https://api.spoonacular.com/recipes/";
    const recipeId = this.$route.params.id;
    const params = "/information?apiKey=";
    fetch(endpoint + recipeId.toString() + params + process.env.VUE_APP_key)
      .then(response => response.json())
      .then(data => {
        this.recipe = data;
        this.loading = false;
      });
  },
  emits: ["favourite-check"],
  methods: {
    favouriteCheck() {
      this.$emit("favourite-check", this.recipe.id);
    }
  }
};
</script>

<style scoped>
.stepList li {
  counter-increment: steps;
}
.stepList li:before {
  font-family: "Poppins";
  content: "Step " counter(steps) ". ";
}

.stepList {
  list-style: none;
  counter-reset: steps;
}
</style>
