<template>
  <div
    class="sticky w-screen h-screen bg-cover"
    :style="{ backgroundImage: `url(${backgroundUrl})` }"
  ></div>
  <div
    class="absolute top-0 left-0 w-screen h-screen flex items-start justify-center pl-10 pr-10 pb-10 overflow-y-scroll"
  >
    <div
      class="relative mt-10 pl-4 pr-4 pt-10 pb-10 sm:p-12 sm:pt-10 rounded-md bg-white bg-opacity-75"
    >
      <img
        class="ml-auto mr-auto mb-4 w-64"
        src="@/assets/images/spoon-logo-no-outline.svg"
      />
      <h1 class="text-center mb-4 w-64 text-2xl" style="font-family: 'Poppins'">
        Recipe Search
      </h1>
      <SearchBox
        :theme="theme"
        v-on:search-submit="searchSubmit"
        itemType="Ingredient"
      />
    </div>
  </div>
</template>

<script>
import SearchBox from "../components/SearchBox.vue";
import smallUrl from "@/assets/images/calum-lewis-vA1L1jRTM70-unsplash-small.jpg";
import medUrl from "@/assets/images/calum-lewis-vA1L1jRTM70-unsplash-med.jpg";

export default {
  components: {
    SearchBox
  },
  data: () => ({
    windowWidth: window.innerWidth
  }),
  computed: {
    backgroundUrl: function() {
      return this.windowWidth < 640 ? smallUrl : medUrl;
    }
  },
  props: {
    allIngredients: Array,
    filteredRecipes: Array,
    favourites: Array,
    loading: Boolean,
    initialSearchPerformed: Boolean
  },
  created() {
    window.addEventListener("resize", this.updateWindowSize);
  },
  unmounted() {
    window.removeEventListener("resize", this.updateWindowSize);
  },
  emits: ["filter-check", "search-submit", "favourite-check"],
  methods: {
    filterCheck(name, checked) {
      this.$emit("filter-check", name, checked);
    },
    searchSubmit(listItems) {
      this.$emit("search-submit", listItems);
      this.$router.push({ name: "results" });
    },
    reroute() {
      this.$router.push({ name: "results" });
    },
    updateWindowSize() {
      this.windowWidth = window.innerWidth;
    }
  }
};
</script>

