<template>
  <p v-if="loadingRecipes">loading...</p>
  <div class="pb-6 h-full bg-gray-200">
    <NavBar
      :favourites="favourites"
      :showFilter="false"
      :backHome="false"
      title="Favourites"
    />

    <div class="large-screen-wrapper pt-6 md:pl-10 md:pr-10 bg-gray-200">
      <RecipesList
        v-on:favourite-check="favouriteCheck"
        :recipeData="favouriteRecipes"
        :favourites="favourites"
        :loading="loading"
        :initialSearchPerformed="initialSearchPerformed"
        :errorMessage="errorMessage"
        :searched="false"
        :fullWidth="true"
      />
    </div>
  </div>
  <FooterComponent />
</template>

<script>
import RecipesList from "../components/RecipesList.vue";
import NavBar from "../components/NavBar.vue";
import FooterComponent from "../components/Footer.vue";

export default {
  components: {
    RecipesList,
    NavBar,
    FooterComponent
  },
  async created() {
    const endpoint = "https://api.spoonacular.com/recipes/";
    const params = "/information?apiKey=";
    for (let recipeId of this.favourites) {
      fetch(endpoint + recipeId + params + process.env.VUE_APP_key)
        .then(response => response.json())
        .then(data => {
          console.log(data);
          this.favouriteRecipes.push(data);
        });
    }
    this.loadingRecipes = false;
  },
  data: () => ({
    favouriteRecipes: [],
    loadingRecipes: true,
    errorMessage: `You haven't added any recipes to your favourites yet.`
  }),
  props: {
    allIngredients: Array,
    filteredRecipes: Array,
    loading: Boolean,
    favourites: Array,
    initialSearchPerformed: Boolean
  },
  emits: ["filter-check", "search-submit", "favourite-check"],
  methods: {
    routeBack() {
      this.$router.go(-1);
    },
    favouriteCheck(recipeId) {
      this.$emit("favourite-check", recipeId);
    }
  }
};
</script>
