<template>
  <div class="sm:flex flex-wrap pl-2 pb-2 pr-2">
    <FilterListItem
      v-for="ingredient of ingredients"
      v-on:filter-check="filterCheck"
      :key="'ingredient-filter-' + ingredient"
      :ingredient="ingredient"
    />
  </div>
</template>

<script>
import FilterListItem from "./FilterListItem.vue";

export default {
  name: "FilterBox",
  components: {
    FilterListItem
  },
  props: {
    ingredients: Array
  },
  data: () => ({
    listItems: []
  }),
  emits: ["filter-check"],
  methods: {
    filterCheck(name, checked) {
      this.$emit("filter-check", name, checked);
    }
  }
};
</script>

