<template>
  <div id="router-wrapper" class="font-sans">
    <!-- <h1 class="m-2 text-lg">recipe search app</h1> -->
    <router-view
      v-on:filter-check="filterCheck"
      v-on:search-submit="searchQuery"
      v-on:favourite-check="favouriteCheck"
      :allIngredients="allIngredients"
      :filteredRecipes="filteredRecipes"
      theme="photo"
      :favourites="favourites"
      :spooky="spooky"
      :loading="loading"
      :initialSearchPerformed="initialSearchPerformed"
    />
  </div>
</template>

<script>
// import Home from "./views/Home.vue";

export default {
  name: "App",
  components: {
    // Home,
  },
  methods: {
    filterCheck(name, checked) {
      if (checked) {
        this.checkedIngredients.push(name);
      } else {
        this.checkedIngredients = this.checkedIngredients.filter(
          ingredient => ingredient != name
        );
        console.log(this.checkedIngredients);
      }
    },
    searchQuery(searchIngredients) {
      this.loading = true;
      this.searchIngredients = searchIngredients;
      const endpoint =
        "https://api.spoonacular.com/recipes/findByIngredients?apiKey=";
      const params =
        "&ingredients=" +
        this.searchIngredients.join(",+") +
        "&number=30&limitLicense=true";
      fetch(endpoint + process.env.VUE_APP_key + params)
        .then(response => response.json())
        .then(data => {
          this.recipes = Array.from(
            data.map(recipe => {
              return {
                id: recipe.id,
                title: recipe.title,
                image: recipe.image,
                ingredients: this.findIngredients(recipe)
              };
            })
          );
          console.log(data);
          this.checkedIngredients = this.allIngredients;
          console.log(this.checkedIngredients);
          this.loading = false;
          this.initialSearchPerformed = true;
        });
    },
    findIngredients(recipe) {
      const ingredientNames = [];
      const ingredients = [];
      for (let ingredient of recipe.usedIngredients) {
        if (!ingredientNames.includes(ingredient.name)) {
          ingredients.push({
            id: ingredient.id,
            name: ingredient.name,
            searched: true
          });
          ingredientNames.push(ingredient.name);
        }
      }
      for (let ingredient of recipe.missedIngredients) {
        if (!ingredientNames.includes(ingredient.name)) {
          ingredients.push({
            id: ingredient.id,
            name: ingredient.name,
            searched: false
          });
          ingredientNames.push(ingredient.name);
        }
      }
      return ingredients;
    },
    favouriteCheck(recipeId) {
      if (this.favourites.includes(recipeId)) {
        this.favourites = this.favourites.filter(id => id != recipeId);
      } else {
        this.favourites.push(recipeId);
      }
    }
  },
  data: () => ({
    searchIngredients: [],
    checkedIngredients: [],
    recipes: [],
    favourites: [],
    spooky: false,
    loading: false,
    initialSearchPerformed: false
  }),
  computed: {
    filteredRecipes() {
      return this.recipes.filter(recipe =>
        recipe.ingredients.every(ingredient =>
          this.checkedIngredients.includes(ingredient.name)
        )
      );
    },
    allIngredients() {
      const ingredientSet = new Set();
      for (let recipe of this.recipes) {
        for (let ingredient of recipe.ingredients) {
          ingredientSet.add(ingredient.name);
        }
      }
      return Array.from(ingredientSet);
    }
  }
};
</script>

