<template>
  <header class="bg-white pt-6 shadow-sm">
    <nav class="large-screen-wrapper">
      <div class="">
        <img
          class="ml-auto mr-auto mb-4 w-64"
          src="@/assets/images/spoon-logo-no-outline.svg"
        />
      </div>
      <div class="flex justify-between md:pl-10 md:pr-10">
        <button
          v-on:click.prevent="routeBack"
          class="flex items-center pl-2 text-gray-800"
        >
          <svg
            class="w-4 h-4"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M15 19l-7-7 7-7"
            ></path>
          </svg>
          <span class="text-sm ml-2">{{
            backHome ? "New Search" : "Back"
          }}</span>
        </button>
        <FavouriteMenuButton :favourites="favourites" />
      </div>
    </nav>

    <div class="mt-4 border-b bg-mustard">
      <div class="large-screen-wrapper pt-4 pl-2 pb-4 pr-2 md:pl-12 md:pr-12 flex items-baseline justify-between">
        <h1 class="text-xl">{{ title }}</h1>
        <button
          v-if="showFilter"
          v-on:click.prevent="toggleFilter"
          class="flex items-center text-gray-700 lg:hidden"
        >
          <span class="text-xs ml-2">Filter</span>
          <svg
            class="w-3 h-3"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M19 9l-7 7-7-7"
            ></path>
          </svg>
        </button>
      </div>
    </div>
  </header>
</template>

<script>
import FavouriteMenuButton from "./FavouriteMenuButton.vue";

export default {
  name: "NavBar",
  components: {
    FavouriteMenuButton
  },
  props: {
    favourites: Array,
    showFilter: Boolean,
    backHome: Boolean,
    title: String
  },
  emits: ["filter-toggle"],
  methods: {
    toggleFilter() {
      this.$emit("filter-toggle");
    },
    routeBack() {
      console.log(this.backHome);
      if (this.backHome) {
        this.$router.push({ name: "home" });
      } else {
        this.$router.go(-1);
      }
    }
  }
};
</script>
