<template>
  <li class="flex justify-between w-full mt-2">
    <span class="border-b-2 border-dashed">{{ itemName }}</span>
    <button
      v-on:click.prevent="removeItem"
      class="w-6 h-6 text-red-400"
      :aria-labelledby="'add-' + itemName + '-button-label'"
    >
      <span :id="'add-' + itemName + '-button-label'" hidden
        >Remove {{ itemName }} from the list.</span
      >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        aria-hidden="true"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
        />
      </svg>
    </button>
  </li>
</template>

<script>
export default {
  name: "SearchListItem",
  props: {
    itemType: String,
    itemName: String
  },
  data: () => ({
    newItem: ""
  }),
  emits: ["remove-item-submit"],
  methods: {
    removeItem() {
      this.$emit("remove-item-submit", this.itemName);
    }
  }
};
</script>

