<template>
  <div v-if="loading" class="p-4 pb-64">loading recipes...</div>
  <div
    v-if="!loading && initialSearchPerformed && recipeData.length === 0"
    class="p-4 pb-64"
  >
    {{ errorMessage }}
  </div>
  <div
    id="recipeList"
    class="flex flex-col items-center md:grid md:grid-cols-2 md:gap-4 pl-4 pr-4"
    :class="fullWidth ? 'lg:grid-cols-3 lg:gap-10' : 'lg:grid-cols-2 lg:gap-10'"
  >
    <RecipeCard
      v-for="recipe of recipeData"
      v-on:favourite-check="favouriteCheck"
      :favourited="favourites.includes(recipe.id)"
      :key="recipe.id"
      :recipe="recipe"
      :searched="searched"
    />
  </div>
</template>

<script>
import RecipeCard from "./RecipeCard.vue";

export default {
  name: "RecipesList",
  components: {
    RecipeCard
  },
  props: {
    recipeData: Array,
    favourites: Array,
    loading: Boolean,
    initialSearchPerformed: Boolean,
    errorMessage: String,
    searched: Boolean,
    fullWidth: Boolean
  },
  data: () => ({}),
  emits: ["favourite-check"],
  methods: {
    favouriteCheck(recipeId) {
      this.$emit("favourite-check", recipeId);
    }
  }
};
</script>

<style scoped></style>
