<template>
  <div
    class="ml-auto mr-auto relative w-64 p-4 pb-2 rounded-md border-opacity-50 shadow-2xl bg-white"
  >
    <h1 class="text-center mb-4">{{ itemType }}s</h1>
    <ul>
      <SearchListItem
        v-on:remove-item-submit="removeItem"
        v-for="(item, index) in listItems"
        :key="index"
        :itemName="item"
      />
    </ul>
    <form class="flex justify-end items-center w-full mt-4">
        <input
          type="text"
          class="bg-gray-100 border-solid border-b-2 min-w-0"
          v-model="newItem"
          placeholder="new ingredient"
        />
        <button
          v-on:click.prevent="addItem"
          class="w-6 h-6"
          :aria-labelledby="'add-' + itemType + '-button-label'"
        >
          <span :id="'add-' + itemType + '-button-label'" hidden
            >Add {{ itemType }} to the list.</span
          >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-full h-full text-gray-700"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M12 6v6m0 0v6m0-6h6m-6 0H6"
            />
          </svg>
        </button>
    </form>

    <button
      v-on:click.prevent="searchSubmit"
      class="searchButton flex justify-around w-3/4 mt-4 p-2 rounded-md shadow-lg shadow-2xl bg-mustard text-gray-800"
    >
      <span class="font-medium">FIND RECIPES</span>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        class="w-6 h-6 ml-2"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2"
          d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
        />
      </svg>
    </button>
  </div>
</template>

<script>
import SearchListItem from "./SearchListItem.vue";

export default {
  name: "SearchBox",
  components: {
    SearchListItem
  },
  props: {
    itemType: String,
    theme: String
  },
  data: () => ({
    newItem: "",
    listItems: []
  }),
  emits: ["search-submit"],
  methods: {
    searchSubmit() {
      const searchTerms = [...this.listItems];
      if (this.newItem != "") {
        searchTerms.push(this.newItem);
      }
      this.$emit("search-submit", searchTerms);
    },
    addItem() {
      if (!this.listItems.includes(this.newItem)) {
        this.listItems.push(this.newItem);
      }
      this.newItem = "";
    },
    removeItem(itemName) {
      const newList = this.listItems.filter(item => item != itemName);
      this.listItems = newList;
    }
  }
};
</script>

<style scoped>
.searchButton {
  bottom: -20px;
  transform: translateX(15%) translateY(70%);
}
</style>
