<template>
  <div class="bg-gray-200">
    <NavBar
      :favourites="favourites"
      :showFilter="true"
      :backHome="false"
      title="Results"
      v-on:filter-toggle="toggleFilter"
    />
    <div class="large-screen-wrapper lg:grid lg:grid-cols-4">
      <div
        class="pt-2 pb-2 shadow-inner border-b bg-white"
        :class="showFilters ? 'block' : 'hidden lg:h-full lg:block'"
      >
        <FilterBox
          v-on:filter-check="filterCheck"
          :ingredients="allIngredients"
        />
      </div>
      <div class="pt-6 pb-6 md:col-span-3 md:pl-10 md:pr-10 bg-gray-200">
        <RecipesList
          v-on:favourite-check="favouriteCheck"
          :recipeData="filteredRecipes"
          :favourites="favourites"
          :loading="loading"
          :initialSearchPerformed="initialSearchPerformed"
          :errorMessage="errorMessage"
          :searched="true"
          :fullWidth="false"
        />
      </div>
    </div>
  </div>
  <FooterComponent />
</template>
<script>
import FilterBox from "../components/FilterBox.vue";
import RecipesList from "../components/RecipesList.vue";
import FooterComponent from "../components/FooterComponent.vue";
import NavBar from "../components/NavBar.vue";

export default {
  name: "Results",
  components: {
    FilterBox,
    RecipesList,
    FooterComponent,
    NavBar
  },
  data: () => ({
    showFilters: false,
    errorMessage: `No recipes fit those criteria... :(  
      Try adding more ingredients, or searching again.`
  }),
  props: {
    allIngredients: Array,
    filteredRecipes: Array,
    favourites: Array,
    loading: Boolean,
    initialSearchPerformed: Boolean
  },
  emits: ["filter-check", "search-submit", "favourite-check"],
  methods: {
    filterCheck(name, checked) {
      this.$emit("filter-check", name, checked);
    },
    routeHome() {
      this.$router.push({ name: "home" });
    },
    toggleFilter() {
      this.showFilters = !this.showFilters;
    },
    favouriteCheck(recipeId) {
      this.$emit("favourite-check", recipeId);
    }
  }
};
</script>
<style scoped></style>
