<template>
  <div
    class="flex justify-between mt-2 first:mt-0 sm:ml-2 p-2 w-full sm:w-auto shadow-sm rounded-lg"
    :class="checked ? 'bg-green-200' : 'bg-gray-200'"
    v-on:click="filterCheck"
  >
    <label v-on:click.prevent="" class="checkbox w-full text-sm">
      <span class="checkbox__label">{{ ingredient }}</span>
      <span class="checkbox__input">
        <input type="checkbox" name="checkbox" :checked="checked" />
        <span class="checkbox__control">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            aria-hidden="true"
            viewBox="0 0 24 24"
            class="text-gray-700"
            stroke="currentColor"
          >
            <path
              v-if="checked"
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
            />
            <path
              v-if="!checked"
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M12 9v3m0 0v3m0-3h3m-3 0H9m12 0a9 9 0 11-18 0 9 9 0 0118 0z"
            ></path>
          </svg>
        </span>
      </span>
    </label>
  </div>
</template>

<script>
export default {
  name: "FilterListItem",
  components: {},
  props: {
    ingredient: String
  },
  data: () => ({
    checked: true
  }),
  emits: ["filter-check"],
  methods: {
    filterCheck() {
      this.checked = !this.checked;
      this.$emit("filter-check", this.ingredient, this.checked);
    }
  }
};
</script>

<style scoped>
/* I modified this accessible custom checkbox styling from a post on moderncss.dev */

.checkbox {
  display: grid;
  align-items: center;
  grid-template-columns: auto min-content;
  grid-gap: 0.5em;
  color: var(--color);
}

.checkbox__control {
  display: inline-grid;
  width: 1em;
  height: 1em;
}

.checkbox__input {
  display: grid;
  grid-template-areas: "checkbox";
}

.checkbox__input > * {
  grid-area: checkbox;
}

.checkbox__input input {
  opacity: 0;
  width: 1em;
  height: 1em;
}

.checkbox__input input:focus + .checkbox__control {
  box-shadow: 0 0 0 0.05em #fff, 0 0 0.15em 0.1em currentColor;
}
</style>
