<template>
  <div
    class="mt-4 w-full sm:w-96 md:w-full md:h-full bg-white overflow-hidden rounded-lg shadow-md"
  >
    <div class="relative bg-gray-200 pb-2/3">
      <router-link v-bind:to="{ name: 'recipe', params: { id: recipe.id } }">
        <img class="absolute w-full h-full object-cover" :src="recipe.image" />
      </router-link>
      <button
        v-on:click.prevent="favouriteCheck"
        class="relative float-right w-8 h-8 p-1 mr-2 mt-2 rounded-full bg-white bg-opacity-90"
        :class="favourited ? 'text-green-500' : 'text-gray-100'"
        :aria-labelledby="`favourite-button-label-${recipe.id}`"
      >
        <svg
          class="w-full h-full"
          fill="currentColor"
          stroke="#48BB78"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="1.5"
            d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
          ></path>
        </svg>
        <span :id="`favourite-button-label-${recipe.id}`" hidden
          >Add recipe to your favourites list</span
        >
      </button>
    </div>
    <div class="p-2">
      <router-link v-bind:to="{ name: 'recipe', params: { id: recipe.id } }">
        <h3 class="inline sm:text-lg mt-2">{{ capitalise(recipe.title) }}</h3>
        <span v-if="recipe.vegan" class="text-green-500 sm:text-lg pl-1"
          >&#x24E5;</span
        >
      </router-link>
      <RecipeInfoBox
        v-if="!searched"
        :recipeId="recipe.id"
        :recipeTime="recipe.readyInMinutes"
        :author="recipe.creditsText"
        :ingredients="searchedIngredients"
      />
      <div v-if="searched">
        <ul>
          <li
            class="pt-2 text-sm"
            v-for="(ingredient, index) of searchedIngredients"
            :key="recipe.id + '-ingredient-' + index"
          >
            <span class="text-green-500">&#8226; </span>
            <span>{{ ingredient.toLowerCase() }} </span>
          </li>
        </ul>
        <ul v-if="showIngredients">
          <li
            class="pt-2 text-sm"
            v-for="(ingredient, index) of unsearchedIngredients"
            :key="recipe.id + '-ingredient-' + index"
          >
            <span class="text-green-500">&#8226;</span>
            {{ ingredient }}
          </li>
        </ul>
      </div>
      <button
        v-if="searched"
        class="float-right mb-2 text-sm text-green-700 italic"
        v-on:click.prevent="toggleIngredients"
      >
        <span v-if="!showIngredients"
          >+ {{ unsearchedIngredients.length }} more</span
        >
        <span v-if="showIngredients">hide extras</span>
      </button>
    </div>
  </div>
</template>

<script>
import RecipeInfoBox from "./RecipeInfoBox.vue";

export default {
  name: "RecipeCard",
  components: {
    RecipeInfoBox
  },
  props: {
    recipe: Object,
    favourited: Boolean,
    searched: Boolean
  },
  computed: {
    searchedIngredients() {
      if (this.searched) {
        return this.recipe.ingredients
          .filter(ingredient => ingredient.searched)
          .map(ingredient => ingredient.name);
      }
      return new Set(
        this.recipe.extendedIngredients.map(ingredient => ingredient.name)
      );
    },
    unsearchedIngredients() {
      if (this.searched) {
        return this.recipe.ingredients
          .filter(ingredient => !ingredient.searched)
          .map(ingredient => ingredient.name);
      }
      return null;
    }
  },
  data: () => ({
    showIngredients: false
  }),
  emits: ["favourite-check"],
  methods: {
    favouriteCheck() {
      this.$emit("favourite-check", this.recipe.id);
    },
    capitalise(text) {
      return text
        .split(" ")
        .map(
          word =>
            word.substring(0, 1).toUpperCase() + word.substring(1).toLowerCase()
        )
        .join(" ");
    },
    toggleIngredients() {
      this.showIngredients = !this.showIngredients;
    }
  }
};
</script>

<style scoped>
img {
  filter: saturate(80%) contrast(120%);
}

.recipeInfoGrid {
  display: grid;
  grid-template-columns: min-content auto;
  grid-gap: 0.5rem;
  align-items: center;
}
</style>
